<template>
  <div class="containerLogin">
    <div style="text-align: center; padding-top: 100px">
      <img src="../../assets/login/pao.svg" width="100" height="100" />
      <div>
        <p class="txt">本机号码一键登录</p>
        <p class="phone">{{ tel }}</p>
        <p class="tip">
          轻触登录即代表您同意<span>《用户协议》</span>与<span
            >《隐私条款》</span
          >
        </p>
      </div>
      <div class="btn" @click="login">立即登录</div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      tel: "",
    };
  },
  mounted() {
    let tel = localStorage.getItem("tel");
    this.tel = tel.replace(/(\d{3})\d{4}(\d{4})/, "$1****$2");
  },
  methods: {
    login() {
      Toast.success("登录成功");
      this.$router.push("/home");
    },
  },
};
</script>

<style scoped>
.containerLogin {
  background: url("~@/assets/login/bgc.png") 100% 100%;
  box-sizing: border-box;
  width: 100%;
  height: 100vh;
  position: relative;
  background-size: cover;
  margin: 0;
}
.txt {
  font-family: "PingFang SC";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  color: #999999;
  margin-top: 10px;
}
.phone {
  font-family: "DINOT";
  font-style: normal;
  font-weight: 500;
  font-size: 32px;
  line-height: 18px;
  color: #212130;
}
.btn {
  height: 50px;
  line-height: 50px;
  width: 90%;
  background: linear-gradient(to right, #a7b5ff, #6e86ff 100%) !important;
  border-radius: 5px;
  color: #fff;
  margin-left: 5%;
}
.tip {
  position: absolute;
  bottom: 30px;
  font-family: "PingFang SC";
  font-size: 12px;
  line-height: 22px;
  color: #999999;
  margin-top: 10px;
  width: 280px;
  left: calc((100vw - 280px) / 2);
}
span {
  color: #4474fb;
}
</style>